import React from 'react'
import { graphql } from 'gatsby'
import Layout from "../components/layout"
import SEO from "../components/seo"
import Img from 'gatsby-image'
import PageWidth from "../components/pagewidth"
//import "./blog-post.css"

const BlogPost = ({ data = {} }) => {
	const { markdownRemark } = data || {}
	const { frontmatter = {}, html = {} } = markdownRemark || {}
  const { cover } = frontmatter
  const { publicURL } = cover || {}

	return (
    <Layout>
      <SEO title="Blog Post" />
  		<article>
        <div style={{maxHeight: '400px', overflow: 'hidden', margin: '0 auto'}}>
          {!!cover ? <Img sizes={cover.childImageSharp.sizes} /> : null}
        </div>
        {/*<div
                  className="blog-post-banner"
                  style={publicURL ? {"backgroundImage": `url(${publicURL})`} : {}}
                ></div>*/}
        <PageWidth style={{paddingTop: '1rem'}}>
          <h1>{frontmatter.title}</h1>
          <p><time>{frontmatter.date}</time></p>
          <div dangerouslySetInnerHTML={{ __html: html }} />
        </PageWidth>
  		</article>
    </Layout>
	)
}

export default BlogPost

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        cover {
          publicURL
            childImageSharp {
              sizes(maxWidth: 2000) {
                ...GatsbyImageSharpSizes
              }
            }
        }
      }
    }
  }
`
